import { Popover, OverlayTrigger, Nav, Form, Button } from "react-bootstrap";

const popover = (
  <Popover id="popover-basic">
    <Popover.Title as="h3">About</Popover.Title>
    <Popover.Content>
    <a href="https://github.com/EBEREGIT/COVID-19-barter-trade">COVID-19 Barter Trade</a> is an innovation brought about due to the fact that Food and other essentials are quite expensive during the COVID-19 pandemic.
      </Popover.Content>
      <Popover.Content>
      <Form>
  <Form.Group controlId="formBasicEmail">
    <Form.Label>Email address</Form.Label>
    <Form.Control type="email" placeholder="Enter email" />
    <Form.Text className="text-muted">
      We'll never share your email with anyone else.
    </Form.Text>
  </Form.Group>

  <Form.Group controlId="formBasicPassword">
    <Form.Label>Password</Form.Label>
    <Form.Control type="password" placeholder="Password" />
  </Form.Group>
  <Form.Group controlId="formBasicCheckbox">
    <Form.Check type="checkbox" label="Check me out" />
  </Form.Group>
  <Button variant="primary" type="submit">
    Submit
  </Button>
</Form>
      </Popover.Content>
      <Popover.Content>
      You may also love to checkout the <a href="https://covid19-stat.netlify.com/">COVID-19 TRACKER</a> I created a while ago.
    </Popover.Content>
    <Popover.Content>
        Created By <a href="https://www.njokusamsonebere.com/">Njoku Samson Ebere</a>
    </Popover.Content>
  </Popover>
);

export default function About() {
  return (
    <div>
      <OverlayTrigger trigger="click" placement="bottom" overlay={popover}>
        <Nav.Link eventKey={3} href="#memes">
          <i class="fa fa-info-circle" aria-hidden="true"></i>{" "}
          <span>About</span>
        </Nav.Link>
      </OverlayTrigger>
    </div>
  );
}
